import React, { Component } from 'react';
import footClass from './footer.module.less';
import { footer } from '../../data/home'
class App extends Component {
    //  相关服务 图片合成一张
    // 底部背景图 缺失
    constructor(props) {
        super(props)
        this.state = {
            ...footer
        }
    }
    render() {
        return <div className={footClass.box}>
            <div className={footClass.main}>
                <div className={footClass.line1}>合作品牌：
                    <img src={require('../../assets/foot_01.png')} alt="" />
                    <img src={require('../../assets/foot_02.png')} alt="" />
                    <img src={require('../../assets/foot_03.png')} alt="" />
                </div>
                <div className={footClass.subsection}>
                    各地分站：
                    {
                        this.state.subsection.map((v, i) => <span key={i}>
                            {v}
                        </span>)
                    }
                </div>
                <div className={footClass.links}>
                    友情链接：
                    {
                        this.state.links.map((v, i) => <span className="btn" key={i}>
                            {v.txt}
                        </span>)
                    }
                </div>
                <div className={footClass.hr}></div>
                <div className={footClass.contact}>
                    <img className={footClass.logo} src={require('../../assets/foot_bglogo.png')} alt="" />
                    <div className={footClass.left}>
                        <div className={footClass['mask-contact']}></div>
                        <div className={footClass.title}>联系我们</div>
                        <img src={require('../../assets/foot_04.png')} alt="" />
                    </div>
                    <div className={footClass.right}>
                        <div className={footClass.line1}>
                            <div>
                                <img src={require('../../assets/foot_05.png')} alt="" />
                                ADDRESS
                            </div>
                            <div>{this.state.addr}</div>
                        </div>
                        <div className={footClass.line2}>
                            <div>
                                <img src={require('../../assets/foot_06.png')} alt="" />
                                EMAIL
                            </div>
                <div>{this.state.email}</div>
                        </div>
                        <div>
                            <div>
                                <img src={require('../../assets/right_03.png')} alt="" />
                                TEL
                            </div>
                            <div>{this.state.tel}
                <span className={footClass.tel}>{this.state.phone}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className={footClass.btm}>{this.state.company}
                <span>{this.state.beianCode}</span>
            </div>
        </div>
    }
}
export default App