import React, { Component } from 'react';
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            list: Array(5).fill('')
        }
    }
    render() {
        return <div className="fixed-right">
            {this.state.list.map((v, i) => <div className="btn" onClick={this.scrollTop.bind(this, i)} key={i}>
                <img src={require(`../../assets/right_0${i + 1}.png`)} alt="" />
            </div>)}
        </div>
    }
    scrollTop(index) {
        let scrollToTop = () => {
            let c = document.documentElement.scrollTop || document.body.scrollTop;
            if (c > 0) {
                window.requestAnimationFrame(scrollToTop);
                window.scrollTo(0, c - c / 8);
            }
        }
        function outIndex() {
            console.log(index)
        }
        let methods = [
            outIndex, outIndex, outIndex, outIndex, scrollToTop
        ]
        // document.body.scrollIntoView()

        methods[index]()
    }
}