import React, { Component } from 'react';
import { news } from '../../../data/home'
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...news
        }
    }

    render() {
        return <div className="news floor">
            <div className="title">新闻动态</div>
            {
                this.state.newsList.map((v, i) => <div key={i} className="news-item btn">
                    <span className="news-title">{v.title}</span>
                    <span>{v.date}</span>
                </div>)
            }
        </div>
    }
}
export default App