import React, { Component } from 'react';
import { floor4 } from '../../../data/home'
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor4
        }
    }

    render() {
        return <div className="floor4 floor">
            <div className="title">
                {this.state.title[0]}<span>{this.state.title[1]}</span>
            </div>
            <div className="description">{this.state.description}</div>
            {
                this.state.list.map((v, i) => <div key={i}>
                    <div className="item-title">
                        <img src={require(`../../../assets/gnts_0${i + 1}.png`)} alt="" />
                        {v.title}
                    </div>
                    <div className="item-description">{v.txt}</div>
                </div>)
            }
            <img className="floor4-img" src={require('../../../assets/gnts_05.png')} alt="" />
            <div className="more-list">
                {
                    this.state.moreList.map((v, i) => <span key={i}>{v}</span>)
                }
            </div>
            <span className="service btn">了解更多</span>
        </div>
    }
}
export default App