import React, { Component } from 'react';
import Home from "../Home/index"
import MobileHome from "../mobile/Home/index"
import './index.css'
import {
    BrowserRouter as Router,
    Switch,
    Redirect,
    Route,
    useHistory,
    useLocation
} from "react-router-dom";

class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            env: 'pc'
        }
    }
    componentDidMount() {
        this.isPc()
    }
    isPc() {
        var sUserAgent = navigator.userAgent.toLowerCase();
        var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";//判断是否为iPad
        var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";//判断是否为iPhone用户
        var bIsMidp = sUserAgent.match(/midp/i) == "midp";
        var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
        var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
        var bIsAndroid = sUserAgent.match(/android/i) == "android";
        var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
        var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
        let env = 'phone'
        if (!(bIsIpad || bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM)) {
            env = 'pc'
        }
        this.setState({
            env
        })
        return env
    }
    render() {
        if (this.state.env === 'pc') {
            return <Home />
            // <Router>
            //     <Switch>
            //         <Route exact path="/Home" component={Home} />
            //         <Redirect from="/*" to={{ pathname: '/Home', state: {} }} />
            //     </Switch>
            // </Router>
        } else {
            return <MobileHome />
            // <Router>
            //     <Switch>
            //         <Route exact path="/Mobile/Home" component={MobileHome} />
            //         <Redirect from="/*" to="/Mobile/Home" />
            //     </Switch>
            // </Router>
        }
    }
}
export default App