import React, { Component } from 'react';
import { news } from '../../data/home'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...news
        }
    }
    render() {
        let title = {
            fontSize: '24px',
            fontWeight: 'bold',
            textAlign: 'center'
        }
        return <div className="news" style={{
            padding: '60px 0',
            backgroundColor: '#333333'
        }}>
            <div className="section-box" style={{
                display: 'flex',
                alignItems: 'stretch',
                color: '#fff'
            }}>
                <div style={{ flexGrow: 1 }}>
                    <div style={title}>新闻动态</div>
                    {
                        this.state.newsList.map((v, i) => <div key={i} className="news-item btn">
                            <span className="title">{v.title}</span>
                            <span>{v.date}</span>
                        </div>)
                    }
                </div>
                <div style={{ width: '2px', backgroundColor: 'rgba(255, 255, 255, 0.2)', margin: '0 105px' }}></div>
                <div style={{ flexGrow: 1 }}>
                    <div style={title}>解决方案</div>
                    {
                        this.state.newsList.map((v, i) => <div key={i} className="news-item btn">
                            <span className="title">{v.title}</span>
                            <span>{v.date}</span>
                        </div>)
                    }
                </div>
            </div>

        </div>
    }
}