import React, { Component } from 'react';
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    render() {
        return <div className="header-floor">
            <img className="logo" src={require('../../../assets/logo.png')} alt="logo" />
            <img className="popup-icon" src={require('../../../assets/top_01.png')} alt="popup" />
        </div>
    }
}
export default App