import React, { Component } from 'react';
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            list: [
                'QQ',
                '微信',
                '留言',
                '咨询',
            ]
        }
    }

    render() {
        return <div className="fixed-floor">
            {this.state.list.map((v, i) => <div key={i}>
                <img src={require(`../../../assets/mobile/foot_0${i + 1}.png`)} alt="" />{v}
            </div>)}
        </div>
    }
}
export default App