import React, { Component } from 'react';
import { floor3 } from '../../../data/home'
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor3
        }
    }

    render() {
        return <div className="floor3 floor">
            <div className="title">
                {this.state.title[0]}<span>{this.state.title[1]}</span>
            </div>
            <div className="description">{this.state.description}</div>
            <img className="floor3-img" src={require('../../../assets/xgfw.png')} alt="" />
        </div>
    }
}
export default App