import React, { Component } from 'react';
import { footer } from '../../../data/home'
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...footer,
        }
    }

    render() {
        return <div className="footer-floor">
            <div className="subsection">
                <div className="title">各地分站：</div>
                {
                    this.state.subsection.map((v, i) => <span key={i}>
                        {v}
                    </span>)
                }
            </div>
            <div className="contact">
                <div className="title">联系我们</div>
                <div>地址：{this.state.addr}</div>
                <div>邮箱：{this.state.email}</div>
                <div>电话：{this.state.tel} <span className="tel">{this.state.phone}</span></div>
            </div>
            <div className="plans">
                <div className="title">解决方案</div>
                <div className="list">
                    {
                        this.state.plans.map((v, i) => <span key={i}>
                            {v}
                        </span>)
                    }
                </div>
            </div>
            <div className="web-info">
                <div>{this.state.company}</div>
                <div>{this.state.beianCode}</div>
            </div>
        </div>
    }
}
export default App