import React, { Component } from 'react';
import { Carousel } from 'antd';
import { floor5 } from '../../../data/home'
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor5
        }
    }

    render() {
        return <div className="floor5 floor">
            <div className="title">
                {this.state.title[0]}<span>{this.state.title[1]}</span>
            </div>
            <div className="description">{this.state.description}</div>
            <Carousel draggable autoplaySpeed={5000} autoplay dots={{ className: 'dot' }}>
                {this.state.list.map((v, i) => <div className="carousel" key={i}>
                    {Array(2).fill('').map((m, n) => <img key={i + '-' + n} src={require(`../../../assets/anli_0${n + 1}.png`)} alt="" />)}
                </div>)}
            </Carousel>
            <div className="app-list">
                {Array(9).fill('').map((v, i) => <img key={i} src={require(`../../../assets/mobile/anli_${3 + i < 10 ? '0' + (3 + i) : 3 + i}.png`)} />)}
            </div>
            <span className="service btn">了解更多</span>
        </div>
    }
}
export default App