import React, { Component } from 'react';
import { floor2 } from '../../../data/home'
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor2
        }
    }

    render() {
        return <div className="floor2 floor">
            <div className="title">
                <span>{this.state.title[0]}</span>{this.state.title[1]}
            </div>
            <div className="description">{this.state.description}</div>
            <div className="content">
                <div className="left">
                    {this.state.list.map((v, i) => {
                        return <div className={i % 2 === 0 ? 'red-circle' : 'origin-circle'} key={i}>
                            {v}
                        </div>
                    })}
                </div>
                <img style={{ width: '50%' }} src={require('../../../assets/jjfa_01.png')} alt="" />
            </div>
        </div>
    }
}
export default App