import React, { Component } from 'react';
import { floor1 } from '../../data/home'
import bg from '../../assets/banner_bg.png'
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor1,
        }
    }
    render() {
        return <div className="floor1">
            <img style={{ width: '100%', display: 'block' }} src={bg} alt="bg" />
            <div className="main">
                <div className="title">{this.state.title}</div>
                <div className="description">{this.state.description}</div>
                <div className="list-box">
                    {this.state.list.map((v, i) => {
                        return <span className="btn" key={i}>
                            <img src={require(`../../assets/banner_0${i + 1}.png`)} alt="" />
                            {v}
                        </span>
                    })}
                </div>
                <span className="service btn">立即咨询</span>
            </div>
        </div>
    }
}
export default App