import React, { Component } from 'react';
import headerClass from './header.module.less';
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            current: 0,
            topNav: [
                {
                    txt: '首页',
                    url: ''
                }, {
                    txt: '应用',
                    url: ''
                }, {
                    txt: '服务',
                    url: ''
                }, {
                    txt: '案例',
                    url: ''
                }, {
                    txt: '资讯',
                    url: ''
                }, {
                    txt: '找物',
                    url: ''
                },
            ]
        }
    }

    render() {
        return <div className={headerClass['header-box']}>
            <div className={headerClass.left}>
                <img className={headerClass.logo} src={require('../../assets/logo.png')} alt="logo" />
                {this.state.topNav.map((v, i) => {
                    return <div key={i} className={headerClass['nav-item'] + ` ${v.txt === '首页' ? '' : headerClass['arrow-right']} ${i === this.state.current ? headerClass.current : ''}`} onClick={this.clickItem.bind(this, i)}>{v.txt}</div>
                })}
            </div>
            <div className={headerClass.right}>
                <div className={headerClass['cart-box']}>
                    <img src={require('../../assets/top_shop.png')} alt="cart" className={headerClass.cart} />
                </div>
                <div className={headerClass.login}>登陆/注册</div>
            </div>
        </div>
    }
    clickItem(current) {
        this.setState({
            current
        })
    }
}
export default App