import React, { Component } from 'react';
import { Carousel } from 'antd';
import { floor5 } from '../../data/home'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor5
        }
    }
    componentWillMount() { }
    render() {
        return <div className="floor4 floor">
            <div className="section-box">
                <div className="title">
                {this.state.title[0]}<span>{this.state.title[1]}</span>
                </div>
                <div className="description">{this.state.description}</div>
                <Carousel draggable autoplaySpeed={5000} autoplay dots={{ className: 'dot' }}>
                    {this.state.list.map((v, i) => <div className="carousel" key={i}>
                        {this.state.list.map((m, n) => <img key={i + '-' + n} src={require(`../../assets/anli_0${n + 1}.png`)} alt="" />)}
                    </div>)}
                </Carousel>
                <div className="service-list">
                    {this.state.serviceList.map((v, i) => <span key={i}>
                        <img src={require(`../../assets/anli_${5 + i < 10 ? '0' + (5 + i) : 5 + i}.png`)} alt="" />
                        <div>
                            <div>经营类目：{v.type}</div>
                            <div className="item-title">{v.title}</div>
                            <div className="item-txt">{v.txt}
                            </div>
                            <div className="link-more btn">了解详情</div>
                        </div>
                    </span>)}
                </div>
                <span className="service btn">了解更多</span>
            </div>
        </div>
    }
}