import React, { Component } from 'react';
import { floor3, floor4 } from '../../data/home'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor3,
            floor4
        }
    }
    render() {
        return <div className="floor3">
            <div className="section-box">
                <div className="title">
                    {this.state.title[0]}<span>{this.state.title[1]}</span>
                </div>
                <div className="description">{this.state.description}</div>
                <img className="floor3-img" src={require('../../assets/xgfw.png')} alt="" />
                <div className="title mt-100">
                    {this.state.floor4.title[0]}<span>{this.state.floor4.title[1]}</span>
                </div>
                <div className="description">{this.state.floor4.description}</div>
                <div className="content">
                    <div className="left">
                        {
                            this.state.floor4.list.map((v, i) => <div key={i}>
                                <div className="item-title">
                                    <img src={require(`../../assets/gnts_0${i + 1}.png`)} alt="" />
                                    {v.title}
                                </div>
                                <div className="item-description">{v.txt}</div>
                            </div>)
                        }
                    </div>
                    <img src={require('../../assets/gnts_05.png')} alt="" />
                </div>
                <div className="more-list">
                    {
                        this.state.floor4.moreList.map((v, i) => <span key={i}>{v}</span>)
                    }
                </div>
                <span className="service btn">了解更多</span>
            </div>
        </div>
    }
}